@import url(https://fonts.googleapis.com/css2?family=Port+Lligat+Sans&display=swap);
/* override browser default */

html,
body {
    margin: 0;
    padding: 0;
}


/* use viewport-relative units to cover page fully */

body {
    height: 100vh;
    width: 100vw;
}


/* include border and padding in element width and height */

* {
    box-sizing: border-box;
}

button {
    border: none;
    outline: none;
    padding: 0px;
}

input {
    outline: none;
    padding: 0px;
    border: 0px;
    margin: 0px;
}
/* fonts */

/* color theme */

:root {   
    
    /* pallete */

    --primary : #0B1726;
    --secondary : #FFFFFF;
    
    /* variants  */
    --primary-2 : #3C419F;
    --secondary-2 : #6C92D3;

    /* tints and shades */
    --tint: #F17B99;

}


#root {
    position: relative;
    height: 100vh;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    min-width: 100vw;
    font-family: "Port Lligat Sans", sans-serif;
    letter-spacing: 0.08rem;
}


.noselect {
    -webkit-touch-callout: none;
    /* iOS Safari */
    -webkit-user-select: none;
    /* Safari */
    /* Konqueror HTML */
    -moz-user-select: none;
    /* Old versions of Firefox */
    -ms-user-select: none;
    /* Internet Explorer/Edge */
    user-select: none;
    /* Non-prefixed version, currently
                                    supported by Chrome, Edge, Opera and Firefox */
}
.dottedPaths {
    stroke-dasharray: 4 12;
    -webkit-animation: dash 30s linear infinite;
            animation: dash 30s linear infinite;
}
  
@-webkit-keyframes dash {
   to {
    stroke-dashoffset: 1000;
  }
}
  
@keyframes dash {
   to {
    stroke-dashoffset: 1000;
  }
}
.logo {
    display: inline-block;
    height: auto;
    width: auto;
    -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
            filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
.logo svg {
    width: 12vh;
}

.logo span {
    position: relative;

    color : var(--primary-2);
    font-size: 36px;
}
/* Simple Button */
.simple-btn {
  position: relative;
  background: var(--primary-2);
  color: black;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-family: inherit;
  font-size: 24px;
  padding: 5px 14px;
  border-radius: 4px;
  overflow: hidden;
  cursor: pointer;
  -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
          filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.simple-btn:active {
  box-shadow: inset 4px 4px 4px rgba(0, 0, 0, 0.25),
    inset -4px -4px 4px rgba(0, 0, 0, 0.25);
}

.simple-btn a {
  /* for <a> we have to put color here */
  color: black;
  text-decoration: none;
}
.simple-btn:hover::before {
  content: "";
  display: inline-block;
  width: 70px;
  height: 100%;

  position: absolute;
  top: 0%;
  left: -100px;

  background: rgb(60, 65, 159);
  background: linear-gradient(
    90deg,
    rgba(60, 65, 159, 0.4) 0%,
    rgba(255, 255, 255, 0.4) 50%,
    rgba(60, 65, 159, 0.4) 100%
  );
  -webkit-animation: 0.6s shine linear;
          animation: 0.6s shine linear;
}

/* 
    You need to add the skew to the animation 
    or else the animation rule will overwrite it. 
*/
@-webkit-keyframes shine {
  0% {
    transform: skewX(30deg) translateX(0%);
  }
  100% {
    transform: skewX(30deg) translateX(300px);
  }
}
@keyframes shine {
  0% {
    transform: skewX(30deg) translateX(0%);
  }
  100% {
    transform: skewX(30deg) translateX(300px);
  }
}

/* Simple Input */

.simple-inp {
  background: var(--secondary-2);
  font-size: 24px;
  font-family: inherit;
  border-radius: 4px;
  /* feel free to overide this  */
  width: 30%;
  text-align: center;
  padding: 5px 14px;
  direction: ltr;
}

.simple-inp::-webkit-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: var(--secondary);
  opacity: 0.4; /* Firefox */
}

.simple-inp:-ms-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: var(--secondary);
  opacity: 0.4; /* Firefox */
}

.simple-inp::-ms-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: var(--secondary);
  opacity: 0.4; /* Firefox */
}

.simple-inp::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: var(--secondary);
  opacity: 0.4; /* Firefox */
}

.simple-inp:-ms-input-placeholder,
.simple-inp::-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  /* Microsoft Edge */
  color: var(--secondary);
}

.mainPage-wrapper {
  position: relative;
  display: flex;
  width: 100%;
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
  height: 100%;
  background-color: var(--primary);
}

.mainPage-wrapper section {
  position: relative;
  height: 100%;
  display: flex;
  justify-content: center;
}

/* left side */
.mainPg-left {
  position: relative;
  display: flex;
  min-width: 43.1%;
  flex-direction: column;
  height: 100%;
  padding-left: 40px;
}

.mainPg-left .logo {
  position: absolute;
  top: 1vh;
}

.mainPg-content {
  display: flex;
  flex-direction: column;
}

.roomId-input > label {
  display: block;
  margin-bottom: 20px;
}
.roomId-input > .simple-btn {
  margin-left: 30px;
}

.mainPg-content > span {
  /* OR */
  color: black;
  font-family: inherit;
  font-size: 24px;
  font-weight: 600;
  margin: 40px 0px;
}

#divider {
  position: absolute;
  left: 0px;
  height: 100%;
  width: 43.1%;
}

/* right side */

.mainPg-right {
  display: inline-block;
  width: 100%;
  left: 0;
  justify-content: center;
  align-items: center;
}
.mainPg-right svg {
  position: relative;
  display: block;
  width: 83%;
}

/* nav bar */

.mainNavBar ul {
  position: absolute;
  top: 0px;
  right: 5vw;
  display: flex;
  list-style: none;
  margin: 0px;
  padding: 1vh 0px;
  background: var(--secondary-2);
  border-radius: 0px 0px 6px 6px;
}

.mainNavBar li {
  margin: 0px 15px;
  font-size: 24x;
  font-weight: 500;
  letter-spacing: 0.1em;
  cursor: pointer;
  color: var(--secondary);
  transition: letter-spacing 0.5s;
}

.mainNavBar li:hover {
  font-weight: 900;
  letter-spacing: 0.3em;
  color: var(--primary);
  text-decoration: underline;
}

@media only screen and (max-width: 769px) {
  .mainPg-right {
    display: none !important;
    padding: 0px;
  }

  .mainPg-left,
  .mainPg-content,
  .roomId-input {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding-left: 0px;
    justify-content: center;
    align-items: center;
  }

  .mainPg-left .logo {
    position: absolute;
    top: 10px;
    left: 20px;
  }

  #divider {
    position: absolute;
    left: 0px;
    height: 100%;
    width: 100%;
  }

  .roomId-input > .simple-btn {
    margin-left: 0px;
    margin-top: 10px;
  }
}

.joinRoom-wrapper {
  position: relative;
  width: 100%;
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
  height: 100%;

  display: flex;
  justify-content: center;

  background-color: var(--primary);
  color: var(--secondary);
  border: 3px solid red;
}

.joinRoom-box {
  position: relative;

  text-align: center;
  max-width: 80%;
  min-width: 50%;
  /* separate header and div */
  display: flex;
  flex-direction: column;
}

.joinRoom-box h2 {
  margin-bottom: 10%;
}

/* separate sections */

.joinRoom-box > div {
  flex-basis: 50%;
  display: flex;
}

.usernameInput,
.memberList {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-basis: 50%;
}

/* who are you */

.usernameInput span {
  margin-bottom: 7%;
}

.usernameInput a,
.usernameInput input {
  margin-bottom: 4%;
}

.usernameInput .simple-inp {
  width: 50%;
  direction: ltr;
}
.usernameInput .simple-inp:focus::-webkit-input-placeholder {
  opacity: 0;
}
.usernameInput .simple-inp:focus:-ms-input-placeholder {
  opacity: 0;
}
.usernameInput .simple-inp:focus::-ms-input-placeholder {
  opacity: 0;
}
.usernameInput .simple-inp:focus::placeholder {
  opacity: 0;
}

.memberList div {
  display: flex;
  max-width: 100%;
  flex-wrap: wrap;
}

/* , and end of each name */

.memberList > div span:not(:last-child)::after {
  content: ",";
  color: var(--tint);
  -webkit-animation: hueChange 4s linear infinite;
          animation: hueChange 4s linear infinite;
}

@-webkit-keyframes hueChange {
  0% {
    -webkit-filter: hue-rotate(0deg);
            filter: hue-rotate(0deg);
  }
  100% {
    -webkit-filter: hue-rotate(360deg);
            filter: hue-rotate(360deg);
  }
}

@keyframes hueChange {
  0% {
    -webkit-filter: hue-rotate(0deg);
            filter: hue-rotate(0deg);
  }
  100% {
    -webkit-filter: hue-rotate(360deg);
            filter: hue-rotate(360deg);
  }
}

.memberList > span {
  margin-bottom: 7%;
}

.memberList > div > span {
  display: block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-bottom: 10px;
  margin-left: 10px;
}

.movableCard {
    position: absolute;
    cursor: -webkit-grab;
    cursor: grab;
}

.movableCard:active {
    cursor: -webkit-grabbing;
    cursor: grabbing;
}
.cardContainer {
  position: relative;
  /* this with defines width of video */
  width: 150px;
  background: grey;
  padding: 20px;
  border-radius: 13px;
}

.cardContainer span {
  display: none;
}

.cardContainer:hover span {
  display: block;
}

.cardContainer span {
  position: absolute;
  bottom: 0;
  -webkit-animation: appear 0.5s ease;
          animation: appear 0.5s ease;
}

@-webkit-keyframes appear {
  0% {
    opacity: 0.3;
    transform: translateY(9px);
  }
  100% {
    opacity: 1;
    bottom: 0;
  }
}

@keyframes appear {
  0% {
    opacity: 0.3;
    transform: translateY(9px);
  }
  100% {
    opacity: 1;
    bottom: 0;
  }
}

video {
  display: block;
  max-width: 100%;
  min-width: 100%;
  height: auto;
  border-radius: 13px;
  outline: none;
}

video::-webkit-media-controls-fullscreen-button,
video::-webkit-media-controls-play-button,
video::-webkit-media-controls-pausebutton {
  display: none;
}

.roomDisplay-wrapper {
  width: 100vw;
  height: 100vh;
  position: relative;
  overflow: hidden;
  background: var(--primary);
}

.roomDisplay-box {
  background-color: var(--primary);
}

/* controll panel */

.controllPanel {
  height: 60px;
  width: 350px;
  position: fixed;
  bottom: 0px;
  left: calc(50% - (350px / 2));
  background-color: var(--primary-2);
  border-radius: 20px 20px 0px 0px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.btnCtrl {
  height: 34px;
  width: 34px;
  border-radius: 50%;
  background-size: cover;
  display: inline-block;
  background-repeat: no-repeat;
  background-color: black;
  margin: 10px;
}

.btnCtrl:hover {
  background-color: gray;
}

